* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  margin: 2px;
/* Used the following properties to enable a red outline around any element to help me identify what changes I'm making */
  /* outline: 1px dashed red; */
  /* outline-offset: 2px; */
}

:root {
  --portfolio-black: #050505;
  --charcoal-gray: #3b3531;
  --portfolio-white: #F1F1F1;
  --portfolio-gray: #A1A1A1;
}

html {
  font-size: 1rem;
}

/* Make images easier to work with */
img,
picture {
max-width: 100%;
display: block;
}

/* Make container into a flex */
.flex {
  display: flex;
}

body {
  font-size: 1rem;
  height: 100%;
  width: 100%;
  margin-bottom: 25px;
  background-image: url(./img/ep_naturalblack.png);
  line-height: 1.5;
}

/* Header and Nav */
.header {
  padding: 20px;
  font-family: 'Francois One', sans-serif;
}

.header h1 {
  display: inline-block;
  font-size: 2.5rem;
  color: var(--portfolio-white);
}

.icon {
  height: 38px;
  float: left;
  margin-right: 10px;
}

.header nav {
  float: right;
  font-family: 'Francois One', sans-serif;
  font-variant: small-caps;
  font-size: 2rem;
  padding-top: 10px;
}

.header nav ul {
  list-style-type: none;
}

.header nav ul li {
  display: inline-block;
  margin-left: 25px;
}

a {
  color: var(--portfolio-gray);
  text-decoration: none;
}

.code:hover {
  text-shadow: 2px 2px 5px var(--portfolio-gray);
}

.code-dark {
  text-shadow: 4px 4px 10px var(--portfolio-gray);
}

main {
  padding: 20px;
  margin-top: 20px;
  margin-bottom: 40px;
  font-size: 1.75rem;
}

/* Hero Styles */
.self-image-cont {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  height: 80vh;
  gap: 20px;
}

.self-image {
  height: 30rem;
  width: 30rem;
  background-image: url(./img/self-image-fed-2.png);
  background-size: cover;
  background-position: center;
}

.title-headers-1 {
  font-size: 401%;
  text-align: center;
  color: var(--portfolio-white);
  font-family: 'Francois One', sans-serif;
  font-variant: small-caps;
}

.title-headers-2 {
  font-size: 200%;
  font-weight: 700;
  text-align: center;
  color: var(--portfolio-white);
  font-family: 'Open Sans', sans-serif;
  font-variant: small-caps;
  line-height: 0;
}

/* Currently not using this selector */
/* .title-headers-3 {
  font-size: 160%;
  font-weight: 300;
  text-align: center;
  color: var(--portfolio-gray);
  font-family: 'Open Sans', sans-serif;
  font-variant: small-caps;
} */

/* About Styles */
.main-headers {
  font-size: 3.75rem;
  text-align: left;
  color: var(--portfolio-gray);
  font-family: 'Francois One', sans-serif;
  text-decoration: underline;
  font-variant: small-caps;
  padding-top: 60px;
}

.container {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  flex-direction: row;
  margin: 20px 0;
  padding: 5px; 
}

.main-p {
  font-family: 'Open Sans', sans-serif;
  color: var(--portfolio-white);
  font-weight: normal;
  text-align: center;
}

.skills-grid {
  display: grid;
  gap: 1.5rem;
  grid-template-columns: repeat(4, 1fr);
  padding-block: 5rem;
  justify-items: center;
}

.skills {
  color: var(--portfolio-white);
}

.custom-icon {
  height: 1.5rem;
  color: var(--portfolio-white);
  padding-right: .5rem;
}

/* Project Container Styles */
.items {
  display: flex;
  align-content: flex-end;
  min-height: 120px;
  min-width: 120px;
  background-color: var(--portfolio-gray);
  flex-basis: calc(50% - 1em);
  border: 3px solid var(--portfolio-white);
  border-radius: 5px;
  margin: 14px;
}

.code-box:hover,
.btn:hover {
  box-shadow: 4px 4px 10px var(--portfolio-gray);
}

.items:first-child {
  min-height: 450px;
  flex-basis: 100%;
}

/* Project Images */
.large-img {
  background-image: url(./img/NFT-Generator-App.gif);
  background-size: cover;
  background-position: top;
}

.project-5 {
  background-image: url(./img/Just-Another-Text-Editor.png);
  background-size: cover;
  background-position: top;
  width: 50%;
  height: 350px;  
}

.project-4 {
  background-image: url(./img/Coding-Tech-Blog-App.png);
  background-size: cover;
  background-position: top;
  width: 50%;
  height: 350px;  
}

.project-3 {
  background-image: url(./img/Movie-Finder-App.png);
  background-size: cover;
  background-position: top;
  width: 50%;
  height: 350px;    
}

.project-2 {
  background-image: url(./img/Weather-Dashboard-App.png);
  background-size: cover;
  background-position: top;
  width: 50%;
  height: 350px;
}

.project-1 {
  background-image: url(./img/Work-Day-Scheduler-App.png);
  background-size: cover;
  background-position: top;
  width: 50%;
  height: 350px;
}

/* Project Buttons/Links */
.app-links {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-end;
  width: 100%;
  color: var(--portfolio-gray);
  padding-bottom: 0.5rem;
}

.deployed-version {
  display: flex;
  font-family: 'Open Sans', sans-serif;
  background-color: var(--portfolio-black);
  font-size: 1.25rem;
  margin: 10px 0px 0px 10px;
  border: 3px solid var(--portfolio-white);
  border-radius: 15px 30px;
  padding: 5px;
}

.github-version {
  display: flex;
  font-family: 'Open Sans', sans-serif;
  background-color: var(--portfolio-black);
  font-size: 1.25rem;
  float: right;
  margin: 10px 10px 0px 0px;
  border: 3px solid var(--portfolio-white);
  border-radius: 30px 15px;
  padding: 5px;
}

/* Contact Info */
.info-style {
  display: flex;
  flex-wrap: wrap;
  gap: 3rem;
}

.react-icons {
  font-size: 3.5rem;
}

.resume {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.contact-div {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  min-height: 400px;
  font-family: 'Francois One', sans-serif;
}

footer {
  color: var(--portfolio-white);
  text-align: center;
}

/* ContactForm */
.form {
  display: flex;
  flex-direction: column;
  /* justify-content: center; */
  align-content: center;
  align-items: center;
  /* border-radius: 20px;
  overflow: hidden;
  max-width: 600px; */
}

.form-control {
  display: flex;
  flex-direction: column;
  width: 40%;
}

.form-control p {
  font-family: inherit;
  font-weight: 800;
  font-size: 12px;
  letter-spacing: 1.2px;
  color: red;
}

.form-input, 
.btn {
  font-family: inherit;
  font-weight: 800;
  font-size: 12px;
  letter-spacing: 1.2px;
  color: var(--portfolio-black);
}

.form-input {
  padding: 2em;

}

.form-input::placeholder {
  opacity: 0.35;
  text-transform: uppercase;
}

.form-input:focus::placeholder {
  color: var(--charcoal-gray);
  opacity: 0.9;
}

.btn {
  background: var(--portfolio-gray);
  color: var(--portfolio-white);
  padding: 1.25em 2em;
  border-radius: 2em;
  margin-top: 1em;
  cursor: pointer;
}

/* Media Queries */
@media (max-width: 990px) {
  .self-image-cont {
      flex-direction: column;
      height: 70vh;
  }
  .completed {
      flex-direction: column;
      align-items: center;
      gap: 10px;
  }
}

@media (max-width: 768px) {
  .self-image {
      height: 15rem;
      width: 15rem;
  }
  .title-headers-1 {
      font-size: 201%;
  }
  .title-headers-2 {
      font-size: 99%;
  }
  .title-headers-3 {
      font-size: 77%;
  }
  .title-headers-5 {
    font-size: 77%;
}
}

/* Used this Media Queries to make the Project 2 bigger when the screen drops below 768px */
/* Used this Media Queries to make the Project 3 bigger when the screen drops below 768px */
@media (max-width: 768px) {
  .project-5,
  .project-4,
  .project-3,
  .project-2,
  .project-1 {
      flex-basis: 100%;
  }
}

/* Used this Media Queries to make the Header and Nav more responsive when the screen drops below 768px */
@media (max-width: 768px) {
  .header {
      display: flex;
      flex-flow: wrap column;
      align-items: center;
  }
  html {
      font-size: .75rem;
  }
  .header nav {
      font-size: 1.5rem;
  }
}

@media (max-width: 768px) {
  .info-style {
      display: flex;
      flex-flow: wrap column;
      align-items: center;
  }

  .contact-div {
      gap: 3rem;
      margin-top: 20px;
  }
}